import React from 'react';
import { useDispatch } from 'react-redux';
import TodayIcon from '@mui/icons-material/Today';
import PeopleAltIcon from '@mui/icons-material/PeopleAlt';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import LoyaltyIcon from '@mui/icons-material/Loyalty';
import StoreIcon from '@mui/icons-material/Store';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';

import { MenuItem } from './index';
import { logoutFetchFromToken } from '../redux/actions';

const Menu = () => {
  const dispatch = useDispatch();
  const handleLogout = () => {
    dispatch(logoutFetchFromToken());
  };
  return (
    <div className="menu">
      <MenuItem Icon={TodayIcon} linkTo="/plan" nameItem="" />
      <MenuItem Icon={StoreIcon} linkTo="/halls" nameItem="" />
      <MenuItem Icon={PeopleAltIcon} linkTo="/clients" nameItem="" />
      <MenuItem Icon={AttachMoneyIcon} linkTo="/finance" nameItem="" />
      <MenuItem Icon={LoyaltyIcon} linkTo="/discount" nameItem="" />
      <MenuItem Icon={ExitToAppIcon} onClick={handleLogout} nameItem="" />
    </div>
  );
};

export { Menu };
